import React from "react";
import styled from "styled-components";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import CheckIcon from "@material-ui/icons/Check";
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import { pink } from "@material-ui/core/colors";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import MoreDialog from "./MoreDialog.js";

document.head.innerHTML +=
  '<link href="http://fonts.googleapis.com/css?family=Great+Vibes" rel="stylesheet">';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(0),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  customWidth: {
    maxWidth: 480,
  },
}));

const Heading = styled.h2`
  font-size: 2.8em;
  text-transform: capitalize;
  text-align: left;
  color: #000;
  font-family: "Great Vibes", cursive;
  font-weight: 500;
  margin-bottom: 20px;
`;

const PinkCheck = () => (
  <CheckIcon
    style={{ color: pink[500], verticalAlign: "middle" }}
    fontSize="large"
  ></CheckIcon>
);

const PictureMore = () => (
  <>
    <Typography variant="h4" style={{ color: "black" }}>
       Print Your Personalized Picture Directly
    </Typography>
    <Typography variant="h6">
      Please provide the printable image with 300DPI (.jpg file) or Vector
      file(.AI/SVG).
    </Typography>
    <br></br>
    <Typography variant="h4" style={{ color: "black" }}>
       Custom Personalized Backdrops
    </Typography>

    <Typography variant="h6">
      If you want to custom the backdrop that from our design,<br></br>
      Please provide us the product link or the image of the product in our
      store or on Amazon, AliExpress platform. <br></br>
      If not,please provide the sample pictures of your ideas.
    </Typography>
  </>
);

const PartThree = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item sm xs>
          <Paper
            className={classes.paper}
            elevation={0}
            style={{ textAlign: "center" }}
          >
            <widgetic-embed
              id="5cf4f33decb2a187408b456c"
              resize="fill-width"
              width="560"
              height="350"
              autoscale="on"
              adaptive="414"
            ></widgetic-embed>
          </Paper>
        </Grid>
        <Grid item sm>
          <Paper
            className={classes.paper}
            elevation={0}
            style={{ padding: "20px" }}
          >
            <Heading>Everything you need to prodive</Heading>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Picture
              <MoreDialog title="Picture">
                <PictureMore></PictureMore>
              </MoreDialog>
            </Typography>

            <Typography variant="h4">
              <PinkCheck></PinkCheck> Size
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Material{" "}
              <MoreDialog title="Material">
                <img
                  style={{ width: "100%" }}
                  src="//cdn.shopifycdn.net/s/files/1/2443/0381/files/1573896867-1_2048x2048.jpg?v=1592634589"
                ></img>
              </MoreDialog>
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Customization Requirements
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Date of your events
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default PartThree;
